body{transform:scale(0.8);transform-origin:0 0;font-family:Noto Sans;font-style:normal;font-weight:800;font-size:21px;line-height:112.8%;letter-spacing:0.05em;text-shadow:-1px 1px 0 #3A4EFF,1px 1px 0 #3A4EFF,1px -1px 0 #3A4EFF,-1px -1px 0 #3A4EFF,0 0 20px #3A4EFF;display:flex;align-items:center;text-align:center;margin:1% 1%;background:black;color:rgba(0,0,0,0.79)}.whiteText{color:white}::-moz-selection{background:rgba(58,78,255,0.5)}::selection{background:rgba(58,78,255,0.5)}.neonBox{border:4px solid #3A4EFF;box-sizing:border-box;box-shadow:0 0 8px 2px #3A4EFF;margin:30px}.R{text-shadow:-1px 1px 0 #E72323,1px 1px 0 #E72323,1px -1px 0 #E72323,-1px -1px 0 #E72323,0 0 20px #E72323}.G{text-shadow:-1px 1px 0 #0BE76F,1px 1px 0 #0BE76F,1px -1px 0 #0BE76F,-1px -1px 0 #0BE76F,0 0 20px #0BE76F}.draggableValue{font-size:41px;color:white}.draggableValue:hover{cursor:ew-resize}html .dragging{cursor:ew-resize}.sliderBox{width:12vw}#editSettings{display:flex;flex-direction:row;align-content:space-around}canvas,img{margin:10px;padding:3px;background-color:rgba(58,78,255,0.3);border:4px solid #3A4EFF;box-sizing:border-box;box-shadow:0 0 8px 2px #3A4EFF}#canvasesContainer{display:flex;flex-direction:row-reverse;justify-content:center}#canvasesRight{display:flex;flex-direction:column;justify-content:space-between}#canvasesRightTop{display:flex;flex-direction:row-reverse;justify-content:flex-end;margin-left:16px}#canvasesRightTopRight{display:flex;flex-direction:column-reverse;justify-content:center}#canvasesRightBot{margin-left:16px}#pixelValues{display:flex;flex-direction:row;justify-content:space-around}.colorValue{width:2em}
